.Xfeed01 {
  padding-top: 0;
}
.Xfeed01-carousel-wrapper {
  position: relative; /* Crucial: This is the positioning context for the buttons */
  /* Add padding here to make space for buttons inside the container,
     or move buttons outside if you prefer. */
  padding: 0 2rem; /* Example: 60px on each side for the buttons */
  /* Remove any overflow: hidden; from here */
}

.Xfeed01-carousel-container {
  /* This is the visible window for the carousel items */
  width: 100%;
  overflow: hidden; /* Hides cards not currently in view */
}

.Xfeed01-carousel-inner {
  display: flex; /* Arranges cards in a row */
  transition: transform 0.5s ease-in-out; /* Smooth sliding animation */
  /* Make sure this element is wider than its parent Xfeed01-carousel-container
     for scrolling to occur. No fixed width here. */
}

.Xfeed01-carousel-item {
  /* Fixed width for each card */
  width: 300px; /* The specified width */

  box-sizing: border-box; /* Include padding/border in width calculation */
  padding: 1rem; /* Padding inside each card */
  flex-shrink: 0; /* Prevents items from shrinking */
  background-color: var(--clr-bg);
  border: 1px solid var(--clr-shadow);
  border-radius: 0.5rem;
  display: flex; /* For aligning content within the card */
  flex-direction: column;
  gap: 0.5rem;
  align-items: start;
  justify-content: start;
  margin-right: 20px; /* Example: 20px space between cards */
}

/* Remove margin-right from the last item to prevent extra space at the end */
.Xfeed01-carousel-inner .Xfeed01-carousel-item:last-child {
  margin-right: 0;
}

.Xfeed01-carousel-item img {
  width: 100%;
  aspect-ratio: 3/2;
  object-fit: cover;
}

.Xfeed01-carousel-item h4 {
}

.Xfeed01-carousel-item p {
}

/* Carousel Buttons */

.carousel-buttons {
  display: flex;
  flex-direction: row;
  gap: 1rem;
}

.carousel-button {
  background-color: var(--clr-bg); /* Semi-transparent background */
  color: var(--clr-2);
  border: none;
  padding: 1rem;
  cursor: pointer;
  font-size: 2rem;
  border-radius: 50%; /* Makes buttons circular */

  transition: background-color 0.3s ease;
  display: flex; /* For perfect centering of arrow glyph */
  align-items: center;
  justify-content: center;
  width: 50px; /* Fixed width and height for circular buttons */
  height: 50px;
}

.carousel-button:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.carousel-button.prev {
  left: 0; /* Position relative to .Xfeed01-carousel-wrapper */
}

.carousel-button.next {
  right: 0; /* Position relative to .Xfeed01-carousel-wrapper */
}

