.Disclaimer {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;

  width: 100%;
  height: 100vh;
  height: 100svh;
  padding: 2rem;

  background-color: #000000cc;
  backdrop-filter: blur(5px);
}

.Disclaimer-box {
  width: 600px;
  padding: 5rem;

  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  text-align: center;

  background-color: var(--clr-bg);
  border: solid 1px var(--clr-shadow);
  border-radius: 0.5rem;
}

.Disclaimer-logo {
  display: flex;
  flex-direction: row;
  gap: 0.5rem;
  justify-content: start;
  align-items: center;
}
.Disclaimer-logo > img {
  width: 1.5rem;
}
.Disclaimer-logo > p {
  color: var(--clr-2);
}

.Disclaimer-box > button {
  width: 200px;
  margin-top: 1rem;
}

/*-- responsive --*/

@media screen and (max-width: 768px) {
  .Disclaimer-box {
    width: 100%;
    padding: 2rem;
  }
}
